<template>
  <div class="ApplicationList">
    <!-- 面包屑 -->
    <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>审批管理</el-breadcrumb-item>
      <el-breadcrumb-item>申请列表</el-breadcrumb-item>
    </el-breadcrumb>-->
    <!-- 日期选择器 -->
    <div class="block">
      <!-- <span class="demonstration">带快捷选项</span> -->
      <el-date-picker
        style="margin-bottom:20px"
        v-model="value2"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions"
        @change="rounddate"
        value-format="yyyy-MM-dd"
      ></el-date-picker>
    </div>
    <!-- 申请表 -->
    <el-table
      :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
      border
      style="width: 100%"
      size="mini"
    >
      <el-table-column sortable label="单据类型">
        <template v-slot:default="{ row }">
          <span v-show="row.incomeExpenseInfo.incomeExpenseType==0">收款单据</span>
          <span v-show="row.incomeExpenseInfo.incomeExpenseType==1">付款单据</span>
          <span v-show="row.incomeExpenseInfo.incomeExpenseType==2">报销单据</span>
        </template>
      </el-table-column>
      <el-table-column prop="userInfo.name" sortable label="申请人" width="180"></el-table-column>
      <el-table-column sortable prop="eventTime" label="申请日期"></el-table-column>
      <el-table-column prop="incomeExpenseInfo.oddNumber" sortable label="收支单据">
        <!-- {{<a href="###">incomeExpenseInfo.oddNumber</a>}} -->
        <template v-slot:default="{ row }">
          <a
            href="###"
            @click="queryInout(row.incomeExpenseInfo.incomeExpenseInfoId)"
          >{{row.incomeExpenseInfo.oddNumber}}</a>
        </template>
      </el-table-column>

      <el-table-column prop label="状态" sortable>
        <template v-slot:default="{ row }">
          <!-- <el-link v-if="row.eventStatus === 0"  type="info"  :underline="false">进行中</el-link> -->
          <el-button v-if="row.eventStatus === 0" type="info" size="small" disabled>进行中</el-button>
          <el-button v-if="row.eventStatus === 1" type="success" size="small" disabled>已完成</el-button>
          <el-button v-if="row.eventStatus === 2" type="danger" size="small" disabled>已拒绝</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="eventComment" label="备注"></el-table-column>
      <el-table-column prop label="操作">
        <template v-slot:default="{ row }">
          <!-- <el-button type="danger" size="small" @click="deldata(row.eventInfoId)">删除</el-button> -->
          <el-button type="primary" size="small" @click="checkdata(row.eventInfoId)">查看进度</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页器 -->
    <!--
      @size-change: 每页条数变化时, 触发
      @current-change: 当前页变化时, 触发
      :current-page 当前页
      :page-sizes 可供选择每页条数的选择项
      :page-size 每页条数
      layout 显示的功能控件
    -->
    <el-pagination
      style="margin-top:10px"
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-sizes="[30, 50, 80]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
    <!-- 查看模态框 -->
    <el-dialog title="当前进度" :visible.sync="dialogVisible" width="40%">
      <el-table border style="width: 100%" :data="checkdataa">
        <el-table-column prop="userInfo.name" label="名字"></el-table-column>
        <el-table-column prop="actExecutionTime" label="时间"></el-table-column>
        <el-table-column prop="actExecutionComment" label="备注"></el-table-column>
        <el-table-column label="状态" sortable>
          <template v-slot:default="{ row }">
            <el-button v-if="row.actExecutionStatus === 0" type="info" disabled size="small">未处理</el-button>
            <el-button v-if="row.actExecutionStatus === 1" type="success" disabled size="small">同意</el-button>
            <el-button v-if="row.actExecutionStatus === 2" type="danger" disabled size="small">拒绝</el-button>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button @click="dialogVisible = false" type="primary">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 单据模态框 -->
    <el-dialog :title="inouttitle" :visible.sync="querydialogVisible" width="40%">
      <div class="rongqi">
        <div class="zhang" :style="zhangyangshi">
          <span>{{zhangyangshitxt}}</span>
        </div>
      </div>
      <!-- <hr> -->
      <table class="zijidanjutb" border="1">
        <tr class="tr">
          <td>单据</td>
          <td colspan="3">{{form1.oddNumber}}</td>
        </tr>
        <tr>
          <td>制单人</td>
          <td>{{form1.userInfo.name}}</td>
          <td>供应商</td>
          <td>{{form1.supplier.supplierName}}</td>
        </tr>
        <tr>
          <td>收款方式</td>
          <td>{{form1.paymentTermName}}</td>
          <td>货币信息</td>
          <td>{{form1.currencyInfo.currencyName}}</td>
        </tr>
        <tr>
          <td>收入金额</td>
          <td>{{form1.incomeAmount}}</td>
          <td>支出金额</td>
          <td>{{form1.expenseAmount}}</td>
        </tr>
        <tr>
          <td>摘要</td>
          <td>{{form1.comment}}</td>
          <td>客户信息</td>
          <td>{{form1.customerInfo.customerName}}</td>
        </tr>
        <tr>
          <td>账号</td>
          <td>{{form1.account}}</td>
          <td>会计科目</td>
          <td>{{form1.accountTitle.accountTitleName}}</td>
        </tr>
        <tr>
          <td>公司信息</td>
          <td>{{form1.companyInfo.companyName}}</td>
          <td>部门</td>
          <td>{{form1.department.name}}</td>
        </tr>
        <tr>
          <td>报销人</td>
          <td>{{form1.expenseUserInfo.name}}</td>
          <td>制单日期</td>
          <td>{{form1.createDate}}</td>
        </tr>
        <tr>
          <td>项目信息</td>
          <td colspan="3">{{form1.projectInfo.projectName}}</td>
        </tr>
        <tr>
          <td colspan="4">产品线分摊表</td>
        </tr>
        <tr>
          <td colspan="2">产品线</td>
          <td>比例</td>
          <td>金额</td>
        </tr>
        <tr v-for="item in form1.ieProductLines " :key="item.ieProductLineId">
          <td colspan="2">{{item.productLine.productLineName}}</td>
          <td>{{item.sumRatio}}</td>
          <td>{{item.practicalSum}}</td>
        </tr>
      </table>
      <el-form ref="form" :model="form1" label-width="80px">
        <!-- <el-form-item label="单据 :">{{form1.oddNumber}}</el-form-item>
        <el-form-item label="制单日期 :">{{form1.createDate}}</el-form-item>
        <el-form-item label="制单人 :">{{form1.userInfo.name}}</el-form-item>
        <el-form-item label="实操人员:">{{form1.actualName}}</el-form-item>
        <el-form-item label="业务人员:">{{form1.clerk}}</el-form-item>-->
        <!-- <el-form-item
          label="供应商 :"
          v-if="form1.supplier.supplierName"
        >{{form1.supplier.supplierName}}</el-form-item>-->
        <!-- <el-form-item label="收款方式 :" v-if="form1.paymentTermName">{{form1.paymentTermName}}</el-form-item>
        <el-form-item label="货币信息 :">{{form1.currencyInfo.currencyName}}</el-form-item>
        <el-form-item label="收入金额 :" v-if="form1.incomeAmount">{{form1.incomeAmount}}</el-form-item>
        <el-form-item label="支出金额 :" v-if="form1.expenseAmount">{{form1.expenseAmount}}</el-form-item>
        <el-form-item label="摘要 :">{{form1.comment}}</el-form-item>
        <el-form-item
          label="客户信息 :"
          v-if="form1.customerInfo.customerName"
        >{{form1.customerInfo.customerName || ''}}</el-form-item>
        <el-form-item label="账   号 :" v-if="form1.account">{{form1.account}}</el-form-item>
        <el-form-item label="会计科目 :">{{form1.accountTitle.accountTitleName}}</el-form-item>
        <el-form-item
          label="公司信息 :"
          v-if="form1.companyInfo.companyName"
        >{{form1.companyInfo.companyName}}</el-form-item>
        <el-form-item
          label="部门 :"
          v-if="form1.department.departmentName"
        >{{form1.department.departmentName}}</el-form-item>
        <el-form-item
          label="项目信息 :"
          v-if="form1.projectInfo.projectName"
        >{{form1.projectInfo.projectName}}</el-form-item>-->
        <!-- <el-form-item> -->
        <!-- <p style="text-align:center;width:100%;margin-bottom:10px;">产品线金额分摊</p>
        <el-table :data="form1.ieProductLines" style="text-align:center;width:100%" border center>
          <el-table-column prop="productLine.productLineName" label="产品线"></el-table-column>
          <el-table-column prop="sumRatio" label="比例"></el-table-column>
          <el-table-column prop="practicalSum" label="金额"></el-table-column>
        </el-table>-->
        <!-- </el-form-item> -->
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="querydialogVisible = false">取 消</el-button>
        <el-button @click="querydialogVisible = false" type="primary">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },

      inouttitle: '',
      startTime: '',
      endTime: '',
      zhangyangshi: '',
      zhangyangshitxt: '',
      value1: '',
      value2: '',
      checkdataa: [],
      form1: {
        expenseUserInfo: { name: '' },
        actualName: '',
        clerk: '',
        incomeExpenseInfoId: '',
        comment: '',
        account: '',
        aa: '',
        oddNumber: '',
        createDate: '',
        createTime: '',
        userInfo: {
          userInfoId: '',
          name: ''
        },
        paymentTermName: '',
        currencyInfo: {
          currencyInfoId: ''
        },
        supplier: { supplierName: '' },
        incomeAmount: '',
        expenseAmount: '',

        customerInfo: {
          customerInfoId: '',
          customerName: ''
        },
        bankInfo: {
          bankInfoId: '',
          bankName: ''
        },
        actInfoId: '',
        accountTitle: {
          accountTitleId: ''
        },
        companyInfo: {
          companyInfoId: '',
          companyName: ''
        },
        department: {
          departmentId: '',
          nameL: ''
        },
        productLine: {
          productLineId: ''
        },
        projectInfo: {
          projectInfoId: ''
        },
        sumRatio: '',
        actExecutions: []
      },

      search: '',
      total: 2,
      page: 1,
      pageSize: 30,
      userInfoId: '',
      tableData: [],
      form: {
        test: 1,
        text:
          'dhaskjdhasjkdhasf打首付款几哈是否会暗示法哈克斯房间号按时发哈市副科级暗示法好假刷卡几号放假'
      },
      dialogVisible: false,
      querydialogVisible: false
    }
  },
  created () {
    this.geteventInfoList()
    // this.queryInout()
  },
  methods: {
    rounddate (i) {
      console.log(i)
      console.log(this.value1, this.value2)
      this.startTime = i[0]
      this.endTime = i[1]
      this.geteventInfoList()
    },
    async deldata (id) {
      // const {data} = await this.$axios.post('',{})
    },
    async checkdata (id) {
      // console.log(id)
      this.dialogVisible = true
      const { data } = await this.$axios.get(
        'hbte-financial/hbte/actExecution/getActExecution',
        { params: { eventInfoId: id } }
      )
      console.log(data.data)
      this.checkdataa = data.data
    },
    async geteventInfoList () {
      const aa = JSON.parse(localStorage.getItem('data'))
      // console.log(aa.userInfoId)
      this.userInfoId = aa.id
      const { data } = await this.$axios.get(
        'hbte-financial/hbte/event/eventInfoList',
        {
          params: {
            page: this.page,
            pageSize: this.pageSize,
            userInfoId: aa.id,
            startTime: this.startTime,
            endTime: this.endTime
          }
        }
      )
      // console.log(data.count)
      this.tableData = data.data
      this.total = data.count
    },
    async queryInout (id) {
      // console.log(id)
      this.querydialogVisible = true
      const { data } = await this.$axios.get(
        'hbte-financial/hbte/incomeExpense/getIncomeExpenseInfo',
        {
          params: {
            incomeExpenseInfoId: id,
            page: this.page,
            pageSize: this.pageSize
          }
        }
      )
      console.log(data.data)
      this.form1 = data.data
      if (data.data.incomeExpenseType == 0) {
        this.inouttitle = '收款单据'
      } else if (data.data.incomeExpenseType == 1) {
        this.inouttitle = '付款单据'
      } else if (data.data.incomeExpenseType == 2) {
        this.inouttitle = '报销单据'
      } else {
        this.inouttitle = '其他单据'
      }
      if (data.data.actStatus == 0) {
        this.zhangyangshitxt = '进行中'
        this.zhangyangshi = 'border:3px solid #ccc;color:#ccc'
      } else if (data.data.actStatus == 1) {
        this.zhangyangshitxt = '已同意'
        this.zhangyangshi =
          'border:3px solid rgb(103, 194, 58);color:rgb(103, 194, 58)'
      } else if (data.data.actStatus == 2) {
        this.zhangyangshitxt = '已拒绝'
        this.zhangyangshi = 'border:3px solid red;color:red'
      } else {
      }
      // this.total = data.data.count
      // console.log(data.data)
      // this.querydialogVisible
    },
    // 只要每页条数变化了, 触发
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      // 更新每页条数
      this.pageSize = val
      // 只要修改了每页条数, 数据所在的页码发生了变化了, 回到第一页
      this.page = 1
      // 重新渲染
      this.geteventInfoList()
    },
    // 只要当前页变化时, 触发函数
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      // 更新当前页
      this.page = val
      // 重新渲染
      this.geteventInfoList()
    }
  }
}
</script>

<style>
.zhang {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: raba(0, 0, 0, 0);
  transform: rotate(-25deg);
  position: relative;
}

.zhang span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 700;
  font-size: 16px;
}
.rongqi {
  position: absolute;
  right: 20px;
  top: 40px;
}
.el-dialog__body {
  padding-top: 10px;
}
</style>
<style style lang='scss'>
.zijidanjutb {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 10px;
  tr {
    height: 30px;
    td,
    th {
      border: 1px solid #000;
    }
    th {
      background-color: #000;
      color: white;
    }
    td {
      width: 25%;
      text-align: center;
    }
  }
}
</style>



